import Form from '../../common/Form';
import Input from '../../common/Form/Input';
import { useState } from 'react';
import Button from '../../common/Button';
import style from './RecoveryPassPage.module.scss';
import { Link } from 'react-router-dom';

const RecoveryPassPage = () => {
  const [activeStyle, setActiveStyle] = useState(false);
  const [changePass, setChangePass] = useState(false);
  const isActive = () => {
    setActiveStyle(true);
  };
  const isChangePass = () => {
    console.log('  ');
    setChangePass(true);
  };

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [repeatPassword, setRepeatPassword] = useState('');

  return (
    <div className={style.card}>
      <h1> </h1>
      <div className={style.steps}>
        <div className={style.active}>
          <span className={`${style.number} ${style.activenumber}`}>1</span>
          <span> </span>
        </div>
        <div className={`${activeStyle && style.active}`}>
          <span className={`${style.number} ${activeStyle && style.activenumber}`}>2</span>
          <span> </span>
        </div>
      </div>
      {!activeStyle ? (
        <Form title="">
          <Input
            title="Email"
            id="email"
            placeholder=" email"
            value={email}
            setValue={setEmail}
          />
          <Button title=" " onClick={isActive} size="x" />
        </Form>
      ) : (
        <Form title=" ">
          <Input
            title="Password"
            id="password"
            placeholder=""
            value={password}
            setValue={setPassword}
          />
          <Input
            title="Repeat Password"
            id="repeatPassword"
            placeholder=" "
            value={repeatPassword}
            setValue={setRepeatPassword}
          />
          <Button title="" onClick={isChangePass} size="x" />
        </Form>
      )}
      {!activeStyle && (
        <p className={style.consent}>
             ,  ,    {' '}
          <Link to="/consent" className={style.link}>
             
          </Link>
          ,{' '}
          <Link to="/consent" className={style.link}>
                
          </Link>{' '}
          {' '}
          <Link to="/consent" className={style.link}>
             
          </Link>{' '}
             .
        </p>
      )}
      {changePass && <p className={style.consent}>  </p>}
    </div>
  );
};

export default RecoveryPassPage;
